  font-family: "Montserrat", "Courier New", "Courier", monospaced;
  font-style: normal;
  font-weight: 400;
  font-size: 16px; }

a {
  text-decoration: none;
  color: #ae7c78; }

img {
  max-width: 100%;
  height: auto; }

/* montserrat-regular - latin */
@font-face {
  font-family: "Montserrat";
