.container {
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

button {
  width: 74.3%;
  padding: 30px 28px;
  text-align: center;
  text-transform: uppercase;
  color: white;
  background-color: #d89999;
  border: 1px solid #d89999;
  border-radius: 40px; }
  button:hover {
    opacity: 0.6; }
  button:active {
    opacity: 1; }

.menu-nav {
