.header-main__logo-image {
  line-height: 70px;
  padding: 10px 10px;
  width: auto; }

.designer-bouquets {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 79px; }

.designer-bouquets__block {
  max-width: 384px; }

.designer-bouquets__heading {
  margin: 0;
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: normal;
  font-size: 44px;
  line-height: 59px;
  color: #ae7c78; }

.designer-bouquets__text {
  margin: 0;
  margin-top: 25px;
  font-size: 24px;
  line-height: 29px; }

.designer-bouquets__btn {
  margin-top: 65px; }

.designer-bouquets__img--tulips {
  margin-top: 60px; }

.designer-bouquets__img--stylist-roses {
  margin-bottom: 41px; }

.advantage__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-self: space-between;
  gap: 20px;
  margin: 0;
  margin-top: 48px;
  padding: 0;
  color: #ae7c78;
  list-style: none; }

.advantage__list__block {
  display: grid;
  grid-template-columns: 1fr 3fr;
  gap: 6px;
