  line-height: 21px; }

.advantage__heading {
  margin: 0;
  margin-bottom: 6px;
  font-size: 16px; }

.advantage__text {
  margin: 0; }

.bouquet {
  display: flex;
  flex-direction: column; }

.popular-bouquets {
  margin-top: 78px;
  padding-left: 30px; }

.bouquet__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 14px;
  max-width: 794px;
  margin: 0 auto;
  margin-top: 35px; }

.bouquet__block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }

.popular-bouquets__heading {
  margin: 0 auto;
  font-family: "Playfair Display";
  font-weight: bold;
  font-size: 36px;
  line-height: 48px;
  letter-spacing: 0.02em;
  color: #ae7c78;
  text-align: center; }

.bouquet__heading,
.bouquet__sale {
  margin: 0;
  font-weight: 500; }

.bouquet__heading {
  margin-top: 26px; }

.bouquet__heading,
.bouquet__sale {
  font-size: 24px; }

.bouquet__sale {
  margin-top: 28px; }

.bouquet__text {
  margin: 0;
  margin-top: 24px;
  font-size: 16px;
  line-height: 20px; }

.bouquet__btn {
  margin-top: 62px; }

.discount__btn {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  margin-top: 6px; }

.discount__btn button {
  padding: 10px;
  background-color: #fff;
  border: #fff; }

.bouquets-catalog {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 19px;
  margin-top: 60px; }

.bouquets-catalog__block {
  position: relative;
  display: block;
  margin: 0;
  min-height: 442px;
  padding: 182px 33px 48px 22px;
  text-align: center;
  color: white;
  background-size: cover;
  border-radius: 10px; }
  .bouquets-catalog__block:hover, .bouquets-catalog__block:focus {
    color: white;
    opacity: 0.5; }
  .bouquets-catalog__block:active {
    color: white;
    opacity: 1; }
  .bouquets-catalog__block::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-color: #ae7c7899;
    background-size: cover;
    z-index: -1; }

.bouquets-catalog__block--bridal::before {
  background-image: url("../img/bridal-bouquet.jpg"); }

.bouquets-catalog__block--peonies::before {
  background-image: url("../img/bouquet-with-peonies.jpg"); }

.bouquets-catalog__block--choose::before {
  background-image: url("../img/choose-a-bouquet.jpg"); }

.bouquets-catalog__heading {
  margin: 0;
  font-family: "Playfair Display";
  font-size: 36px;
  line-height: 48px; }

.bouquets-catalog__text {
  margin: 9px 0 0; }

.discount {
  display: grid;
